* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-size: 100%;
}
a {
    text-decoration: none;
    color: black;
}
a,
button {
    cursor: pointer;
}

li {
    list-style-type: none;
}
h1 {
    font-size: 2.5rem;
}
h2 {
    font-size: 2rem;
}
h3 {
    font-size: 1.5rem;
}
